<template>
  <section id="open">
    <img src="../assets/logo.svg" class="logo">
    <h1 class="welcome-header">Select the newsletter</h1>
    <ul class="newsletters-list">
      <li class="newsletters-list__item" 
          v-for="newsletter in newsletters" 
          v-bind:class="{ 'selected': isSelected(newsletter) }" 
          v-on:click="select(newsletter)">
        {{ newsletter }}
      </li>
    </ul>
    <button v-link="'editor/'+ selected" v-bind:disabled="!selected"  class="button">Edit</button>
  </section>
</template>

<script>
import {getNewsletters} from "../vuex/actions";

export default {
  data () {
    return {
      selected: ""
    };
  },

  vuex: {
    state: {
      newsletters: state => state.newsletters
    },
    actions: {getNewsletters}
  },

  route: {
    data ({ to }) {
      this.getNewsletters();
    }
  },

  methods: {
    isSelected (item) {
      return this.selected && this.selected === item;
    },

    select (item) {
      this.selected = item;
    }
  }
};
</script>

<style lang="scss">
  @import "../assets/scss/button.scss";

  #open{
    max-width: 600px;
    margin: 0px auto;
    text-align: center;
  }

  .newsletters-list{
    border-radius: 2px 2px 0 0;
    border: 1px solid #eee;
    border-bottom: none;
    list-style: none;
    margin: 0px 0;
    padding: 0;
    

    &__item{
      color: #666;
      padding: 10px 20px 8px;
      border-bottom: 1px solid #eee;
      cursor: pointer;
      
      &:last-child{
        border-bottom: none;
      }

      &:hover{
        background: #eee;
      }

      &.selected{
        background: rgba(#287bbb, .3);
      }
    }
  }
</style>



