<template>
  <section id="editor">
    <sidebar></sidebar>
    <preview></preview>
  </section>
</template>

<script>
import sidebar from "./sidebar";
import preview from "./preview";
import {getNewsletter} from "../../vuex/actions";

export default {
  components: {
    sidebar,
    preview
  },

  vuex: {
    actions: {getNewsletter}
  },

  route: {
    data ({ to }) {
      this.getNewsletter(to.params.newsletter);
    }
  }
};
</script>

<style lang="scss">
  #editor{
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    display: flex;
  }
</style>



