<template>
  <section id="sidebar">
    <nav class="nav">
      <a class="image-button" v-link="'/'"><img src="../../assets/icons/home.svg" width="20"></a>
      <a class="image-button" v-link="'/open'"><img src="../../assets/icons/open.svg" width="22"></a>
      <a class="image-button" v-link="'/new'"><img src="../../assets/icons/new_file.svg" width="18"></a>
      <a class="image-button" v-on:click="save()"><img src="../../assets/icons/save.svg" width="18"></a>
      <a class="image-button" v-on:click="del()"><img src="../../assets/icons/trash.svg" width="15"></a>
    </nav>
    <div class="sidebar-blocks">
      <div v-for="block in newsletter.blocks">
        <accordeon v-if="block.type == 'header'" title="Newsletter Header">
          <newsletter-header v-bind:content.sync="block"></newsletter-header>
        </accordeon>
        <accordeon v-if="block.type == 'articles'" title="Articles">
          <articles v-bind:content.sync="block"></articles>
        </accordeon>
        <accordeon v-if="block.type == 'destination'" title="Destination">
          <destination v-bind:content.sync="block"></destination>
        </accordeon>
      </div>
    </div>
  </section>
</template>

<script>
import {getNewsletter, deleteNewsletter, saveNewsletter} from "../../vuex/actions";
import textField from "./blocks/ui/text-field";
import accordeon from "./blocks/ui/accordeon";

import newsletterHeader from "./blocks/newsletter-header";
import articles from "./blocks/articles";
import destination from "./blocks/destination";

export default {
  vuex: {
    state: {
      newsletter: state => state.newsletter
    },
    actions: {
      getNewsletter,
      deleteNewsletter,
      saveNewsletter
    }
  },

  components: {
    textField, accordeon, newsletterHeader, articles, destination
  },

  methods: {
    del () {
      this.deleteNewsletter(this.newsletter.name);
      this.$router.go("/open");
    },

    save () {
      this.saveNewsletter(this.newsletter);
    }
  }
};
</script>

<style lang="scss">
  @import "../../assets/scss/button.scss";

  #sidebar{
    margin-right: 10px;
    border-right: 1px solid #ddd;
    width: 400px;
    position: relative;
    background: #e9f2f8;
  }

  a{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 56px;
    height: 56px;
    cursor: pointer;

    &:hover{
      background: #c9e3f4;
    }
  }

  .nav{
    display: flex;
    padding: 0px;
    border-bottom: 1px solid #c2c2c2;
    z-index: 1000;
  }

  .sidebar-blocks{
    position: absolute;
    top: 56px;
    left: 0;
    width: 100%;
    bottom: 0;
    overflow: hidden;
    box-sizing: border-box;
    overflow-y: scroll;

  }

</style>



